<template>
    <div class="ebook">
      <title-bar :ifTitleAndMenuShow="ifTitleAndMenuShow"></title-bar>
        <div class="read-wrapper">
          <div id="read"></div>
          <div class="mask">
            <div class="left" @click="prePage"></div>
            <div class="center" @click="toggleTitleAndMenu"></div>
            <div class="right" @click="nextPage"></div>
          </div>
        </div>
        <menu-bar :ifTitleAndMenuShow="ifTitleAndMenuShow"
                  :fontSizeList="fontSizeList"
                  :defaultFontSize="defaultFontSize"
                  @setFontSize="setFontSize"
                  :themeList="themeList"
                  :defaultTheme="defualtTheme"
                  @setTheme="setTheme"
                  :bookAvailable="bookAvailable"
                  @onProgressChange="onProgressChange"
                  :navigation="navigation"
                  @jumpTo="jumpTo"
                  ref="menuBar"></menu-bar> <!-- 给组件传递参数 ,ref属性可以使用this.$refs像dom一样引用-->
    </div>
</template>

<script>
import TitleBar from '@/components/TitleBar'
import MenuBar from '@/components/MenuBar'
import Epub from 'epubjs'
global.ePub = Epub
const DOWNLOAD_URL = '../static/2018_Book_AgileProcessesInSoftwareEngine.epub'
export default {
  components: { // 注册组件
    TitleBar,
    MenuBar
  },

  data() {
    return {
      ifTitleAndMenuShow: false,
      fontSizeList: [
        {fontSize: 12},
        {fontSize: 14},
        {fontSize: 16},
        {fontSize: 18},
        {fontSize: 20},
        {fontSize: 22},
        {fontSize: 24}
      ],
      defaultFontSize: 16,
      themeList: [
        {
          name: 'default',
          style: {
            body: {
              'color': '#000', 'background': '#fff'
            }
          }
        },
        {
          name: 'eye',
          style: {
            body: {
              'color': '#000', 'background': '#ceeaba'
            }
          }
        },
        {
          name: 'night',
          style: {
            body: {
              'color': '#ccc', 'background': '#000'
            }
          }
        },
        {
          name: 'gold',
          style: {
            body: {
              'color': '#000', 'background': 'rgb(241, 236, 226)'
            }
          }
        }
      ],
      defualtTheme: 0,
      bookAvailable: false,
      navigation: {}
    }
  },
  methods: {
    jumpTo(href) {
      this.rendition.display(href)
      this.hideTitleAndMenu()
    },
    hideTitleAndMenu() {
      this.ifTitleAndMenuShow = false
      this.$refs.menuBar.hideSetting()
      this.$refs.menuBar.hideContent()
    },
    // 进度条数值
    onProgressChange(progress) {
      const percentage = progress / 100
      const location = percentage > 0 ? this.locations.cfiFromPercentage(percentage) : 0
      this.rendition.display(location)
    },
    setTheme(index) {
      this.themes.select(this.themeList[index].name)
      this.defualtTheme = index
    },
    registerTheme() {
      this.themeList.forEach(theme => {
        this.themes.register(theme.name, theme.style)
      })
    },
    setFontSize(fontSize) {
      if (this.themes) {
        this.defaultFontSize = fontSize
        this.themes.fontSize(fontSize + 'px')
      }
    },
    toggleTitleAndMenu() {
      this.ifTitleAndMenuShow = !this.ifTitleAndMenuShow
      if (!this.ifTitleAndMenuShow) {
        this.$refs.menuBar.hideSetting()
      }
    },

    prePage () {
      if (this.rendition) {
        this.rendition.prev()
      }
    },
    nextPage () {
      if (this.rendition) {
        this.rendition.next()
      }
    },
    // 电子书的解析和渲染
    showEpub () {
      // 生成Book
      this.book = new Epub(DOWNLOAD_URL)
      // 生成Rendition对象
      this.rendition = this.book.renderTo('read', {
        width: window.innerWidth,
        height: window.innerHeight
      })
      // 通过Rendition.display渲染电子书
      this.rendition.display()
      // this.themes.register(name, styles)注册主题
      // this.themes.select(name) 选择启用主题
      this.themes = this.rendition.themes
      this.registerTheme()
      this.setTheme(this.defualtTheme)
      //  获取locations对象
      this.book.ready.then(() => {
        // this.navigation.toc[index].href 即是该目录对应的链接
        this.navigation = this.book.navigation
        return this.book.locations.generate()
      }).then(result => {
        this.locations = this.book.locations
        this.bookAvailable = true
      })
    }
  },
  // vue的构造函数
  mounted () {
    this.showEpub()
  }
}
</script>
<style lang="scss" scoped>
@import "assets/styles/global";
.ebook{
  position:relative;
  .read-wrapper{
    .mask{
      position:absolute;
      top:0;
      left:0;
      display:flex;
      z-index:100;
      width:100%;
      height:100%;
      .left{
        flex:0 0 px2rem(100);
      }
      .center{
        flex:1;
      }
      .right{
        flex:0 0 px2rem(100);
      }
    }
  }
}
</style>



